import request from "@/utils/Request";
import md5 from 'js-md5'
import Qs from 'qs'

// isLogin
export function isLogin() {
    return request({
        url: 'admin/islogin',
        method: 'post',
    })
}

// org
export function getOrg(username, password) {
    const data = {
        userid:username,
        passwd:md5(password)
    }
    return request({
        url: 'admin/org',
        method: 'post',
        headers:{
            "Content-Type":"application/x-www-form-urlencoded; charset=UTF-8"
        },
        data: Qs.stringify(data)
    })
}

// 登录方法
export function login(username, password, agenid) {
    const data = {
        KEYDATA:`username${username},tlxx,${md5(password)}`,
        AGENID:agenid,
        t: '1'
    }
    return request({
        url: 'admin/check',
        method: 'post',
        headers:{
            "Content-Type":"application/x-www-form-urlencoded; charset=UTF-8"
        },
        data: Qs.stringify(data)
    })
}