<template>
  <div class="login">
    <el-form class="login-form" :model="loginForm" :rules="rules" ref="loginForm">
      <h3 class="title">企业互联网生态平台</h3>
      <el-form-item prop="username">
        <el-input class="mini" v-model="loginForm.username" clearable @blur="getOrg()">
          <template slot="prepend"><i class="el-icon-s-custom"></i></template>
        </el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" class="mini" v-model="loginForm.password" clearable @blur="getOrg()" @input="initAgen" >
          <template slot="prepend"><i class="el-icon-lock"></i></template>
        </el-input>
      </el-form-item>

      <el-form-item prop="agenid">
        <div style="display: inline-block;height: 40px;vertical-align: top;">
          <div class="agenid" style="width: 55px;text-align: center;padding: 0"><i
              class="el-icon-office-building"></i></div>
        </div>
        <el-select style="width: 340px" v-model="loginForm.agenid" :disabled="aglist.length <= 1">
          <el-option v-for="item in aglist" :label="item.agname" :value="item.fragid" :key="item.fragid"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" style="width: 400px" @click="login('loginForm')" >登录</el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
import {getOrg, isLogin, login} from "@/api/login";

export default {
  name: "Login",
  data() {
    return {
      loginForm: {
        username: '',
        password: '',
        agenid: ''
      },
      rules:{
        username: [
          { required: true, message: '请输入工号', trigger: 'blur' },
          { min: 4, max: 4, message: '长度4个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        agenid: [
          { required: true, message: '请选择机构', trigger: 'change' }
        ]
      },
      aglist: []
    }
  },
  methods: {
    initAgen(){
      this.aglist = [];
      this.loginForm.agenid = '';
    },
    getOrg() {
      if (this.loginForm.username == '' || this.loginForm.password == '') return;
      getOrg(this.loginForm.username, this.loginForm.password ).then(response => {
        if(response.dblist.length > 1){
          this.aglist = response.dblist;
          this.loginForm.agenid = '';
        }else if(response.dblist.length == 1){
          this.aglist = response.dblist;
          this.loginForm.agenid = response.dblist[0].fragid;
        }else {
          this.initAgen();
        }
      })
    },
    login(loginForm){
      this.$refs[loginForm].validate((valid) => {
        if (valid) {
          login(this.loginForm.username,this.loginForm.password,this.loginForm.agenid).then(res =>{
            if("success" == res.result)this.$router.push({path: '/'})
            else this.$router.push({path: '/login'})
          })
          alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    }
  },
  mounted() {
    isLogin().then(() => {
      this.$router.push({path: '/'})
    }).catch(e=>{
      console.log(e + "jdslfjs")
    })
  }
}
</script>

<style>
.login {
  margin: auto;
  width: 400px;
  margin-top: 200px;
}

.title {
  margin: 0px auto 30px auto;
  text-align: center;
  color: #707070;
}

.login-form {
  border-radius: 6px;
  background: #ffffff;
  width: 400px;
  padding: 25px 25px 5px 25px;
}

.agenid {
  background-color: #F5F7FA;
  color: #909399;
  vertical-align: middle;
  display: table-cell;
  position: relative;
  border: 1px solid #DCDFE6;
  border-radius: 4px;
  padding: 0 20px;
  width: 1px;
  white-space: nowrap;
  margin-left: 0;
  line-height: 38px;
}
</style>